.about {
  padding-bottom: 48px;
  display: flex;
  flex-direction: column;
  height: 800px;
}

.about-top {
  display: flex;
  justify-content: space-between;
  padding: 0 10%;
  align-items: center;
}

.picture {
  height: 420px;
}
.about-info {
  font-size: 60px;
  width: 600px;
}
.info-name {
  color: #0043d4;
  font-weight: 900;
  font-style: italic;
}
.about-photo {
  width: 600px;
  height: 500px;
}

@media only screen and (max-width: 1480px) {
  .about {
    height: 700px;
  }
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-info {
    display: flex;
    justify-content: center;
  }
  .about-photo {
    width: 400px;
    height: 400px;
  }
  .picture {
    height: 280px;
  }
  .about-info {
    font-size: 40px;
  }
  .info-name {
    font-size: 26px;
  }
}

@media only screen and (max-width: 720px) {
  .about {
    height: 600px;
  }
  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-photo {
    width: 300px;
    height: 300px;
  }
  .about-info {
    font-size: 40px;
    width: 300px;
    display: flex;
    justify-content: center;
  }
  .picture {
    height: 280px;
  }
  .about-info {
    font-size: 18px;
  }
  .info-name {
    font-size: 26px;
  }
}
